* {
  margin: 0;
  box-sizing: border-box;
}

/* ---------------- Navbar and burger menu ------------------ */
nav {
  width: 100%;
  height: 5em;
  display: flex;
  justify-content: flex-start;
  padding: 1em;
}

.burger-menu {
  height: 100%;
  width: 4em;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  cursor: pointer;
}

.burger-bar {
  width: 3.5em;
  height: 0.5em;
  background-color: #FFF;
  border-radius: 0.5em;
}

.menu {
  width: 100%;
  background-color: #1B1B1F;
  position: absolute;
  z-index: -1;
  left: 0;
}

/* ------------- sliding menu ------------------ */
.hidden {
  display: none;
}

.visible {
  display: inherit;
}

/* ----- animations--------- */
/* clicked */
.burger-bar.clicked:nth-child(1){
  -webkit-transform: rotate(45deg) translate(0.75em, 1.25em);
          transform: rotate(45deg) translate(0.75em, 1.25em);
  transition: ease-out 0.5s;
}

.burger-bar.clicked:nth-child(2){
  -webkit-transform: scale(0);
          transform: scale(0);
  transition: ease-out 0.5s;
}

.burger-bar.clicked:nth-child(3){
  -webkit-transform: rotate(135deg) translate(-0.5em, 1em);
          transform: rotate(135deg) translate(-0.5em, 1em);
  transition: ease-out 0.5s;
}

/* unclicked */
.burger-bar.unclicked {
  -webkit-transform: rotate(0) translate(0);
          transform: rotate(0) translate(0);
  transition: cubic-bezier(0.175, 0.885, 0.32, 1.275) 0.5s;
}

.burger {
  display: none;
}

@media screen and (max-width: 768px) {
  .burger {
    display: block;
  }

  .burger__menu {
    flex-direction: column;
    padding: 0;
    list-style: none;
    left: 0;
  }

  .burger__menu-item {
    border-bottom: solid 1px #FFF;
    padding: 10px;
  }

  .burger__menu-item:hover {
    color: #D9B972;
    font-weight: bold;
  }

  .burger-menu {
    margin-top: -4px;
  }

  .burger-menu.clicked {
    margin-top: -4px;
  }
}


@media screen and (max-width: 425px) {
  .burger {
    display: block;
  }

  .burger-bar {
    width: 2.5em;
    height: 0.3em;
  }

  .burger-menu {
    width: 2.5em;
    height: 70%;
    margin-top: 4px;
  }

  .burger-menu.clicked {
    height: 93%;
    margin-top: -4px;
  }
}

